export default function AddToCart(picked){
    let firstDish = document.querySelector('.add-to-cart1')
    firstDish.addEventListener('click', () => {
        picked.push({item: 'Couscous', price: '$30'})
        console.log(picked.length)
        document.querySelector('.cart-span').innerText = picked.length
    })
       document.querySelector('.add-to-cart2').addEventListener('click', () => {
        picked.push({item: 'Tagine Beef', price: '$33'})
        document.querySelector('.cart-span').innerText = picked.length
    })
    document.querySelector('.add-to-cart3').addEventListener('click', () => {
        picked.push({item: 'Harira Soup', price: '$18'})
        document.querySelector('.cart-span').innerText = picked.length
    })
    document.querySelector('.add-to-cart4').addEventListener('click', () => {
        picked.push({item: 'Beef With Prunes', price: '$16'})
        document.querySelector('.cart-span').innerText = picked.length
    })
    document.querySelector('.add-to-cart5').addEventListener('click', () => {
        picked.push({item: 'Chicken With Lemon', price:'$22'})
        document.querySelector('.cart-span').innerText = picked.length
    })
    return picked
}