import ismail from './img/ismail.jpeg'
import vanessa from './img/vanessa.jpeg'
import dean from './img/dean.jpg'

export default function contact(){
    const div = document.createElement('div')

    
    const contacts = document.createElement('div')
    contacts.classList.add('contacts')
    const title = document.createElement('h1')
    title.innerText = 'Contact Us'
    
    const ismailDiv = document.createElement('div')
    ismailDiv.classList.add('card')
    
    const vanessaDiv = document.createElement('div')
    vanessaDiv.classList.add('card')

    const deanDiv = document.createElement('div')
    deanDiv.classList.add('card')

    const ismailTitle = document.createElement('h2')
    const vanessaTitle = document.createElement('h2') 
    const deanTitle = document.createElement('h2') 

    ismailTitle.innerText = 'Ismail Bardach'
    vanessaTitle.innerText = 'Vanessa Kuek'
    deanTitle.innerText = 'Dean Bardach'

    const ismailContact = document.createElement('div')
    const vanessaContact = document.createElement('div')
    const deanContact = document.createElement('div')
    ismailContact.classList.add('spans')
    vanessaContact.classList.add('spans')
    deanContact.classList.add('spans')

    ismailContact.innerHTML = `
    <span>Branch Manager</span>
    <span>9376 8062</span>
    <span>Daddy@Atlas.com</span>
    `
    vanessaContact.innerHTML = `
    <span>Chef</span>
    <span>2404 1990</span>
    <span>Mama@Atlas.com</span>
    `
    deanContact.innerHTML = `
    <span>CEO</span>
    <span>1411 2023</span>
    <span>Boss@Atlas.com</span>
    `

    const ismailImg = new Image();
    ismailImg.src = ismail;

    const vanessaImg = new Image();
    vanessaImg.src = vanessa;

    const deanImg = new Image();
    deanImg.src = dean;

    ismailDiv.appendChild(ismailTitle)
    ismailDiv.appendChild(ismailContact)
    ismailDiv.appendChild(ismailImg)

    vanessaDiv.appendChild(vanessaTitle)
    vanessaDiv.appendChild(vanessaContact)
    vanessaDiv.appendChild(vanessaImg)

    deanDiv.appendChild(deanTitle)
    deanDiv.appendChild(deanContact)
    deanDiv.appendChild(deanImg)

    contacts.appendChild(title)
    contacts.appendChild(ismailDiv)
    contacts.appendChild(vanessaDiv)
    contacts.appendChild(deanDiv)

    div.appendChild(contacts)

    return div.innerHTML
}