*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
} 
body{
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    border: 50px solid transparent;
    border-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) 30 round;
}

.content {
    height: 100%;
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}
/* BAR */
.bar{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 20px;
}

.bar img {
    width: 50px;
    cursor: pointer;
}

.bar > div{
    justify-self: center;
}

.bar button{
    border: none;
    font-size: 2rem;
    font-weight: bold;
    border-radius: 18px;
    background-color: rgb(229, 241, 237);
    cursor: pointer;
    color:black;
    margin-left: 20px;
}

.bar button:hover {
    scale: 0.97;
    color: rgb(94, 92, 92);
}

.bar span {
    color:red;
    font-size: 1.3rem;
}

/* MAIN */

.main {
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 50px;
    font-size: 1.5rem;
}

.main div{
    width: 700px;
    border: 5px solid transparent;
    border-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) 30 round;
    padding: 5px;
}

.para {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.para::before {
    content: open-quote;
}
.para::after {
    content: close-quote;
}

.main h1{
    font-size: 2rem;
}

/* SECOND PAGE */

.content {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 100%;
}

.main-dishes {
    display: flex;
    flex-direction: column;
    gap: 30px;
}
.main-dishes div{
    width: 50vw;
    border: 10px solid transparent;
    border-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) 30 round;
    padding: 20px;
    display: grid;
    gap: 10px;
}

.main-dishes img{
    width: 45vw;
}

.main-dishes h2 {
    color: brown;
    text-align: center;
    font-size: 3rem;
    text-transform: uppercase;
}

.main-dishes h3{
    font-size: 2rem;
    color: darkgreen;
}

.main-dishes p {
    font-size: 1.3rem;
}

.main-dishes h4{
    font-size: 3rem;
    color: gold;
}

.main-dishes button{
    padding: 10px;
    border-radius: 8px;
    border-style: none;
    cursor: pointer;
    background-color: rgb(90, 247, 90);
    font-size: 1.2rem;
    font-weight: 600;
    width: 200px;
    justify-self: center;
}

/* Contacts page */

.contacts {
    display: flex;
    flex-direction: column;
    gap: 50px;
}

.contacts .card {
    padding: 5px;
    border: 10px solid transparent;
    border-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) 30 round;
}
.contacts h1 {
    text-align: center;
    font-size: 3rem;
    padding: 10px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

.contacts img {
    width: 400px;
    border-radius: 26px;
}

.contacts .spans {
    display: flex;
    flex-direction: column;
    font-size: 1.5rem;
    margin: 10px 0px;
}

/* CART */

.receipt{
    width: max-content;
    height: max-content;
    min-height: 40vh;
    min-width: 40vw;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 20px 20px 20px 20px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.3);
    background-color: rgb(242, 239, 239);
}

.receipt h1 {
    margin-bottom: 20px;
}

.receipt .bill-content{
    font-size: 1.5rem;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.receipt p {
    display: flex;
    gap: 50px;
}

.receipt .submit{
    padding: 10px;
    font-size: 1rem;
    border: none;
    background-color: lightgreen;
    cursor: pointer;
    font-weight: bolder;
    border-radius: 8px;
    margin-top: 10px;
}

.submit:hover{
    scale: 0.97;
    background-color: rgb(157, 231, 157);
