export default function showCart(picked){
    const div = document.createElement('div')
    div.classList.add('container')
    const receipt = document.createElement('div')
    receipt.classList.add('receipt')
    const billTitle = document.createElement('h1')
    billTitle.innerText = 'Your Order:'
    const billContent = document.createElement('div')
    billContent.classList.add('bill-content')
    const submitOrder = document.createElement('button')
    submitOrder.classList.add('submit')
    submitOrder.innerText = 'Submit'
    
    const bill = picked.map(item => `<p><span>${item.item}</span> <span>${item.price}</span></p>`
    )
    for (let i = 0; i < picked.length; i++){
        billContent.innerHTML += bill[i]
    }
    receipt.appendChild(billTitle)
    receipt.appendChild(billContent)
    receipt.appendChild(submitOrder)
    div.appendChild(receipt)
    return div.innerHTML
}